export default {
	data() {
		const currentDate = this.getDate({
			format: true
		});
		return {
			bian: true, //更多选项boo
			shui: true, //报价含税
			yunfei: true, //报价含税
			fapiao: ['增值税发票', '其他发票'], //发票数组
			fapiaoindex: 0, //发票index
			date: currentDate, //当前日期
			date02: currentDate, //当前日期
			shengming:true, //声明
			jiadata: [
				{
					cao: '',
					images: ''
				}
			]
		};
	},
	methods: {
		// 展示发票要求
		showyaoqiu: function() {
			var that = this;
			uni.showActionSheet({
				itemList: that.fapiao,
				success: function(res) {
					that.fapiaoindex = res.tapIndex;
				},
				fail: function(res) {
					console.log(res.errMsg);
				}
			});
		},
		// 修改报价要求
		change: function(type) {
			if (type == 0) {
				this.shui = !this.shui;
			} else if (type == 1) {
				this.yunfei = !this.yunfei;
			}else if (type == 2) {
				this.shengming = !this.shengming;
			}
		},
		//点击删除，删除添加内容
		shan: function(index) {
			this.jiadata.splice(index, 1);
		},
		//添加内容，点击展示下面内容
		jia: function() {
			this.jiadata.push({
				cao: '',
				images: ''
			});
		},
		//跳转页面
		opennew: function(id) {
			uni.navigateTo({
				url: '../' + id + '/' + id
			});
		},
		//点击展开更多默认选项 展示下面内容
		moren: function() {
			this.bian = !this.bian;
		},
		// 上传图片
		choseimage: function(index) {
			var that = this;
			uni.chooseImage({
				count: 1,
				success: chooseImageRes => {
					that.jiadata[index].images = chooseImageRes.tempFilePaths[0];
					const tempFilePaths = chooseImageRes.tempFilePaths;
					uni.uploadFile({
						url: 'https://www.example.com/upload', //仅为示例，非真实的接口地址
						filePath: tempFilePaths[0],
						name: 'file',
						success: uploadFileRes => {
							console.log(uploadFileRes.data);
						}
					});
				}
			});
		},
		// 获取当前时间
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year;
			} else if (type === 'end') {
				year = year + 10;
			}
			month = month > 9 ? month : '0' + month;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
		// 获取截止报价日期回调
		bindDateChange: function(e) {
			this.date = e.target.value;
		},
		// 获取最迟收货日期回调
		bindDateChange02: function(e) {
			this.date02 = e.target.value;
		}
	},
	computed: {
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		}
	}
};
